<template>
	<div class="add">
		<div class='head'>
			<router-link to="/info" tag="div">
				<van-icon name="arrow-left" @click="tui()"/>
			</router-link>
			<div>编辑地址</div>
			<div>编辑</div>
		</div>
		<div class="main">
			<div class="dizhi">
				<div>
					<div>明德楼</div>
					<div>111000000</div>
				</div>
				<div>
					<div>x</div>
				</div>
			</div>
			<router-link to="/Added" tag="div" class="myorder">
				<div>新增地址</div>
				<div>
					<van-icon name="arrow"/>
				</div>
			</router-link>
		</div>
	</div>
</template>
<script>
	export default {
		name: "Add",
		methods: {
			tui() {
				this.$router.go(-1)
			}
		}

	}
</script>
<style lang="scss" scoped>
	.add {
		height: 100%;
		display: flex;
		flex-direction: column;
	}

	.head {
		height: 1.4rem;
		background: #3190e8;
		color: white;
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-weight: 600;
		> div:nth-of-type(1) {
			margin-left: 0.5rem;
			font-size: 0.45rem;
		}
		div:nth-of-type(2) {
			font-size: 0.45rem;
		}
		div:nth-of-type(3) {
			margin-right: 0.5rem;
			font-size: 0.45rem;
		}
	}

	.main {
		flex-grow: 1;
		overflow: auto;
	}

	.dizhi {
		width: 100%;
		height: 1.5rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 0.3rem;
		border-top: 1px solid gainsboro;
		border-bottom: 1px solid gainsboro;
		font-size: 0.45rem;
		> div:nth-of-type(1) {
			margin-left: 0.5rem;
			div {
				font-size: 0.45rem;
			}
		}
		> div:nth-of-type(2) {
			margin-right: 0.5rem;
			div {
				font-size: 0.45rem;
			}
		}
	}

	.myorder {
		width: 100%;
		height: 1.3rem;
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-top: 0.3rem;
		border-top: 1px solid gainsboro;
		border-bottom: 1px solid gainsboro;
		> div:nth-of-type(1) {
			margin-left: 0.5rem;
			font-size: 0.45rem;
		}
		> div:nth-of-type(2) {
			margin-right: 0.5rem;
			font-size: 0.45rem;
			i {
				margin-top: 0.15rem;
			}
		}

	}

</style>



